import { BlogContent, BlogPostFile } from '@youfoundation/transit-lib';
import { useState } from 'react';
import { useNavigate, useParams } from 'react-router-dom';
import { convertTextToSlug } from '../../../helpers/common';
import { t } from '../../../helpers/i18n/dictionary';
import useBlog from '../../../hooks/blogs/useBlog';
import useChannels from '../../../hooks/blogs/useChannels';
import BlogArticleCreator from '../../Blog/BlogArticleCreator/BlogArticleCreator';
import BlogEditor from '../../Blog/BlogEditor/BlogEditor';
import ActionButton from '../../Buttons/ActionButton';
import ConfirmDialog from '../../Dialog/ConfirmDialog/ConfirmDialog';
import PublishDialog from '../../Dialog/PublishDialog/PublishDialog';
import PageMeta from '../../Layout/PageMeta/PageMeta';

const BlogDetail = () => {
  const navigate = useNavigate();
  const { blogKey } = useParams();
  const [{ data: channels, isLoading: isChannelsLoading }] = useChannels();
  const [needsConfirmation, setNeedsConfirmation] = useState(false);
  const [wantsPublish, setWantsPublish] = useState(false);

  const {
    fetch: { data: blog, isLoading: isBlogLoading },
    save: { mutate: saveBlog, status: saveBlogStatus },
    remove: { mutate: removeBlog },
  } = useBlog({
    blogSlug: blogKey,
  });

  const saveBlogFile = (blogFile: BlogPostFile<BlogContent>) => {
    saveBlog(blogFile, {
      onSuccess: (data) => {
        // If caption changed, then slug changes at the moment.. So we need to navigate
        // TODO Decide if we want to work with fileId, Content Id, Slugs
        // TODO When slug is stored as well, we still need to navigate, but when slug changes
        if (
          !blog ||
          (blogKey !== convertTextToSlug(blogFile.content.caption) &&
            blogKey !== blogFile.fileId?.toString())
        ) {
          const targetPath = blogFile.content.caption?.length
            ? convertTextToSlug(blogFile.content.caption)
            : data.toString();
          const path = window.location.pathname.split('/');
          path.pop();
          if (path && targetPath) {
            navigate(`${path.join('/')}/${targetPath}`);
          }
        }
      },
    });
  };

  if (!channels || isChannelsLoading || isBlogLoading) {
    return <>Loading</>;
  }

  return (
    <>
      <section>
        <PageMeta
          title={blog ? <>{blog.content.caption}</> : t('New blog')}
          actions={
            <>
              <ActionButton
                type="remove"
                className="my-auto ml-auto"
                icon="trash"
                onClick={() => setNeedsConfirmation(true)}
              />
              <ActionButton
                type="primary"
                className="my-auto ml-2"
                onClick={() => setWantsPublish(true)}
              >
                {t('Publish')}
              </ActionButton>
            </>
          }
          breadCrumbs={[
            { href: '/owner/blog', title: 'Blog' },
            { title: blog.content?.caption ?? '' },
          ]}
          saveStatus={saveBlogStatus}
        />

        <div className="md:mx-auto md:max-w-[40rem]">
          {blog ? (
            <BlogEditor blog={blog} saveBlog={saveBlogFile} />
          ) : (
            <BlogArticleCreator saveBlog={saveBlogFile} />
          )}
        </div>
      </section>
      {blog ? (
        <>
          <ConfirmDialog
            title={`${t('Remove')} ${blog?.content.caption ?? ''}`}
            confirmText="Permanently remove"
            needConfirmation={needsConfirmation}
            onConfirm={() =>
              removeBlog(
                { fileId: blog.fileId, slug: blog.content.slug },
                {
                  onSuccess: () => {
                    const path = window.location.pathname.split('/');
                    path.pop();
                    if (path) {
                      navigate(`${path.join('/')}`);
                    }
                  },
                }
              )
            }
            onCancel={() => {
              setNeedsConfirmation(false);
            }}
          >
            <p className="text-sm text-gray-500">
              {t('Are you sure you want to remove this blog? This action cannot be undone.')}
            </p>
          </ConfirmDialog>
          <PublishDialog
            isOpen={wantsPublish}
            onCancel={() => setWantsPublish(false)}
            onConfirm={() => setWantsPublish(false)}
            blogFile={blog}
            channels={channels}
            title={`${t('Publish')}: ${blog.content.caption}`}
          />
        </>
      ) : null}
    </>
  );
};

export default BlogDetail;
