import { useMutation, useQuery, useQueryClient } from '@tanstack/react-query';
import {
  ApiType,
  BlogContent,
  BlogPostFile,
  Client,
  PublishTarget,
} from '@youfoundation/transit-lib';
import { convertTextToSlug } from '../../helpers/common';
import useAuth from '../auth/useAuth';
import useStaticFiles from '../staticFiles/useStaticFiles';

type useBlogProps = {
  channelSlug?: string;
  blogSlug?: string;
};

const useBlog = ({ blogSlug }: useBlogProps = {}) => {
  const { getSharedSecret } = useAuth();
  const client = new Client({ api: ApiType.Owner, sharedSecret: getSharedSecret() });

  const queryClient = useQueryClient();
  const { mutate: publishStaticFiles } = useStaticFiles().publish;

  const fetchBlog = async ({ blogSlug }: useBlogProps) => {
    if (!blogSlug) {
      return;
    }

    // Find direct
    const directMatch = await client.blogPostProvider.getBlogPostFile(blogSlug);
    if (directMatch) {
      return directMatch;
    }

    // Find draft
    const pageSize = 100;
    const foundDrafts = await client.blogPostProvider.getMasterPosts(undefined, pageSize);

    // Process blog posts (Only until fixed in data upload)
    const foundDraft = foundDrafts.find(
      (curr) => convertTextToSlug(curr.content.caption) === blogSlug
    );
    if (foundDraft) {
      return foundDraft;
    }

    // Nothing found...
    return;
  };

  const saveData = async (blogFile: BlogPostFile<BlogContent>) => {
    return await client.blogPostProvider.saveBlogPostMaster(blogFile);
  };

  // slug property is need to clear the cache later, but not for the actual removeData
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  const removeData = async ({ fileId, slug }: { fileId: string; slug: string }) => {
    return await client.blogPostProvider.removeBlogPost(fileId);
  };

  const publishBlog = async ({
    blogFile,
    publishTargets,
  }: {
    blogFile: BlogPostFile<BlogContent>;
    publishTargets: PublishTarget[];
  }) => {
    return await client.blogPostProvider.publishBlogPost(blogFile, publishTargets);
  };

  const unPublishBlog = async ({
    blogFile,
    channelId,
  }: {
    blogFile: BlogPostFile<BlogContent>;
    channelId: string;
  }) => {
    if (await client.blogPostProvider.deleteFromChannel(channelId, blogFile.content.id)) {
      // Update blog master to remove the publishTarget
      const newTargets = blogFile.publishTargets.reduce((currArr, target) => {
        if (target.channelId === channelId) {
          return currArr;
        } else {
          return [...currArr, target];
        }
      }, []);

      const updatedBlogFile: BlogPostFile<BlogContent> = {
        ...blogFile,
        publishTargets: newTargets,
      };

      return await saveData(updatedBlogFile);
    }
  };

  return {
    fetch: useQuery(['blog', blogSlug], () => fetchBlog({ blogSlug }), {
      refetchOnMount: false,
      refetchOnWindowFocus: false,
    }),
    save: useMutation(saveData, {
      onSuccess: (_data, variables) => {
        if (variables.content.slug) {
          queryClient.invalidateQueries(['blog', variables.content.slug]);
        } else {
          queryClient.invalidateQueries(['blog']);
        }
        queryClient.removeQueries(['blogs']);
      },
    }),
    remove: useMutation(removeData, {
      onSuccess: (_data, variables) => {
        if (variables && variables.slug) {
          queryClient.invalidateQueries(['blog', variables.slug]);
        } else {
          queryClient.invalidateQueries(['blog']);
        }
        queryClient.removeQueries(['blogs']);
      },
    }),
    publish: useMutation(publishBlog, {
      onSuccess: () => {
        queryClient.invalidateQueries(['blog']);
        queryClient.removeQueries(['blogs']);

        publishStaticFiles();
      },
    }),
    unpublish: useMutation(unPublishBlog, {
      onSuccess: () => {
        queryClient.invalidateQueries(['blog']);
        queryClient.removeQueries(['blogs']);

        publishStaticFiles();
      },
      onError: (er) => {
        console.error(er);
      },
    }),
  } as const;
};

export default useBlog;
